                        :items="grados_discapacidad" item-text="grado_discapacidad.descripcion"
                        item-value="grado_discapacidad_id" :rules="[(v) => (!!v || gradoVacio) || 'Seleccione una opción']"
                        :disabled="gradoVacio" filled></v-select>
                    </v-col>
                    <v-col cols="12" sm="4">
                      <v-text-field label="Porcentaje(%)" type="number" v-model.trim="postulante.grado"
                        :rules="[(v) => !!v || 'El campo es requerido']" filled></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="4">
