                          <v-text-field v-model="disabilityDateFormatted" label="Fecha de carnet" append-icon="mdi-calendar"
                            :rules="[(v) => !!v || 'El campo es requerido']" filled readonly v-bind="attrs" v-on="on">
                          </v-text-field>
