                <div v-else-if="type_state === 3">
                  <span class="red--text">
                    No hay registro con
                    <b>Cédula Identidad o DNA y Fecha de Nacimiento</b> ingresada.
                  </span>
                </div>
                <div v-else-if="type_state === 4">
                  <span class="red--text">
                    <b>Su información ya no puede ser modificada, puesto que está en la siguiente fase.</b>
                  </span>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
