      <div v-else>
        <notify-card :title="notify_title" :load="notify_load" process="el proceso de admisión"></notify-card>
      </div>
    </v-container>
    <v-dialog v-model="confirmDialog" persistent width="300">
      <v-card>
        <v-card-title class="headline grey lighten-3" primary-title>
          <span class="headline">Alerta</span>
        </v-card-title>
        <v-card-text class="text-center">
            <p>¿Esta seguro(a) de modificar su información?</p>
            <p>
              Verifique sus datos antes de enviar, no existirá reclamos posteriores.
            </p>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="success"
            :loading="send_loading"
            @click="savePostulante"
          >
            Aceptar
          </v-btn>
          <v-btn color="error" @click="confirmDialog = false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import HeaderTitle from "@/components/HeaderTitle";
import NotifyCard from "@/components/NotifyCard";
import Service from "../utils/general";
import { mapActions } from "vuex";
import axios from "axios";

export default {
  name: "formulario-modificacion",
  components: {
    HeaderTitle,
    NotifyCard,
  },
  data: () => ({
    notify_title: "Cargando",
    notify_load: true,
    load_search: false,
    send_loading: false,
    disability_date: false,
    menu_date: false,
    type_state: 1,
    convocatoria: {
      id: null,
    },
    modalidad: {
      id: null,
    },
    generos: [],
    departamentos: [],
    provincias: [],
    municipios: [],
    cantones: [],
    localidades : [],
    lenguas: [],
    nacion_pueblos: [],
    puestos: [],
    competencias: [],
    competencia_gestiones: [],
    discapacidades: [],
    grados_discapacidad: [],
    instituciones: [],
    especialidades: [],
    gradoVacio: false,
    postulante: {
      id: "",
      persona_id: "",
      cedula_dni: "",
      complemento:"",
      paterno: "",
      materno: "",
      nombres: "",
      fecha_nacimiento: "",
      institucion: "",
      especialidad: "",
      convocatoria : "",
      modalidad: "",
      unidad_educativa: "",
      gestion: "",
      informacion_academica : "",
      departamento_id: "",
      direccion_departamento_id:"",
      provincia_id: "",
      municipio_id: "",
      localidad_id: "",
      institucion_id: "",
      especialidad_id: "",
      lengua_id: "",
      pueblo_nacion: "",
      nacion_pueblo_id:"",
      estado_id: "",
      modalidad_id : "",
      convocatoria_id : "",
      direccion_zona:"",
      direccion_calle:"",
      direccion_nro:"",
      ruta_imagen: "",
      descripcion: "",
      tipo: "",
      foto_act: "",
      foto: null,
      codigo:""
    },
    
    canton_id:"",
    confirmDialog: "",
    fFotografia: null, // <- initialize the v-model prop
    imageData: null,  
    reglasImage: [
      v => !!v || 'El archivo es Requerido',
      v => (v && v.size > 0) || 'El archivo es Requerido',
    ], 
  }),
  computed: {
    birthDateFormatted() {
      if (!this.postulante.fecha_nacimiento) return null;

      const [year, month, day] = this.postulante.fecha_nacimiento.split("-");
      return `${day}/${month}/${year}`;
    },
    disabilityDateFormatted() {
      if (!this.postulante.fecha_emision) return null;

      const [year, month, day] = this.postulante.fecha_emision.split("-");
      return `${day}/${month}/${year}`;
    },
  },
 
  mounted() {
    this.getModalidades();
    Service.removeModality();
  },
  methods: {
    ...mapActions(["toast"]),
    getModalidades() {
      this.notify_load = true;
      axios
        .get(Service.getUrlAPI() + "admision/modalidad", Service.getHeader())
        .then((response) => {
          this.convocatoria = response.data.convocatoria
            ? response.data.convocatoria
            : { id: null, cite: "" };
          this.notify_title = "Notificación";
          this.notify_load = false;
        //  this.getParameters();
        })
        .catch(() => {
          this.notify_load = false;
          console.error("Parametros no encontrado");
        });
    },
    getParameters() {
      axios
        .post(
          Service.getUrlAPI() + "admision/parametro/registro",
          { convocatoria_id: this.convocatoria.id,
          modalidad_id: this.modalidad.id},
          Service.getHeader()
        )
        .then((response) => {
          this.departamentos = response.data.departamentos;
          this.generos = response.data.generos;
          this.nacion_pueblos = response.data.nacion_pueblos;
          
          if(this.postulante.sigla_modalidad === "A"){
            this.lenguas = response.data.lenguas;
            this.instituciones = response.data.instituciones;
          }
          if (this.postulante.sigla_modalidad === "B3") {
            this.discapacidades = response.data.discapacidades;
            
            this.lenguas = response.data.lenguas;
          }
          if (this.postulante.sigla_modalidad === "B2") {
            this.competencias = response.data.tipo_competencias;
            this.lenguas = response.data.lenguas;
          }
        })
        .catch(() => {
          console.error("Parametros no encontrado");
        });
    },
    getSpecialties(param) {
      this.especialidades = [];
      if (param == 0) {
        this.postulante.especialidad_id = "";
      }
      axios
        .post(
          Service.getUrlAPI() + "institucion/especialidades",
          { institucion_id: this.postulante.institucion_id },
          Service.getHeader()
        )
        .then((response) => {
          this.especialidades = response.data;
        })
        .catch(() => {
          console.error("Especialidades no encontrado");
        });
    },
    getEscuelas(b=true) {
      this.instituciones = [];
      if(b){
        this.postulante.institucion_id = "";
      }
      if (this.postulante.sigla_modalidad === "B1" || this.postulante.sigla_modalidad === "B4") {
        axios
        .post(
          Service.getUrlAPI() + "admision/parametro/instituciones",
          {
            convocatoria_id: this.postulante.convocatoria_id,
            modalidad_id: this.postulante.modalidad_id,
            lengua_id: this.postulante.lengua_id,
            departamento_id : this.postulante.direccion_departamento_id
          },
          Service.getHeader()
        )
        .then((response) => {
          this.instituciones = response.data.instituciones;
        })
        .catch(() => {
          console.error("Escuelas no encontradas");
        });
      }
      if (this.postulante.sigla_modalidad === "B2" || this.postulante.sigla_modalidad === "B3") {
        axios
        .post(
          Service.getUrlAPI() + "admision/parametro/instituciones",
          {
            convocatoria_id: this.postulante.convocatoria_id,
            modalidad_id: this.postulante.modalidad_id,
            lengua_id: 1,//NINGUNA //this.postulante.lengua_id,
            departamento_id : 11 //OTRO this.postulante.direccion_departamento_id
          },
          Service.getHeader()
        )
        .then((response) => {
          this.instituciones = response.data.instituciones;
        })
        .catch(() => {
          console.error("Escuelas no encontradas");
        });
      }
      
    },
    getLenguas(b=true) {
      if (this.postulante.sigla_modalidad === "B1" || this.postulante.sigla_modalidad === "B4") {
        this.lenguas = [];
        if(b){
          this.postulante.lengua_id = "";
        }

        axios
        .post(
          Service.getUrlAPI() + "admision/parametro/lenguas",
          {
            convocatoria_id: this.postulante.convocatoria_id,
            modalidad_id: this.postulante.modalidad_id,
            departamento_id: this.postulante.direccion_departamento_id
          },
          Service.getHeader()
        )
        .then((response) => {
          this.lenguas = response.data.lenguas;
        })
        .catch(() => {
          console.error("Escuelas no encontradas");
        });
      }
    },
    getGradoDiscapacidad(b=true) {
      this.grados_discapacidad = [];
      if(b){
        this.postulante.grado_discapacidad_id = "";
      }
      axios
        .post(
          Service.getUrlAPI() + "discapacidad/grado_discapacidad",
          { discapacidad_id: this.postulante.discapacidad_id },
          Service.getHeader()
        )
        .then((response) => {
          this.grados_discapacidad = response.data;
          if (this.grados_discapacidad.length == 1 && this.grados_discapacidad[0].grado_discapacidad.descripcion == "") {
            this.postulante.grado_discapacidad_id = this.grados_discapacidad[0].grado_discapacidad_id;
            this.gradoVacio = true;
          } else {
            this.gradoVacio = false;
          }

        })
        .catch(() => {
          console.error("Grados de discapacidad no encontrado");
        });
    },
    cargaConvocatoria(){
      axios
        .get(
          Service.getUrlAPI() +
            "admision/obtener/convocatoria/" +
            this.convocatoria.id,
          Service.getHeader()
        )
        .then((response) => {
          this.postulante.convocatoria = response.data.gestion.toString();
        })
        .catch(() => {
          console.error("datos no encontrado");
        });
    },
    searchPostulante() {
      if (this.$refs.formp.validate()) {
        this.load_search = true;
        this.type_state = 1;
        axios
          .post(
            Service.getUrlAPI() + "admision/postulante/datos",
            {
              convocatoria_id: this.convocatoria.id,
              cedula_dni: this.postulante.cedula_dni,
              fecha_nacimiento: this.postulante.fecha_nacimiento,
              complemento : this.postulante.complemento?this.postulante.complemento:"",
              codigo : this.postulante.codigo,
            },
            Service.getHeader()
          )
          .then((response) => {
            this.load_search = false;
            if (response.data.id == null) {
              this.type_state = 3;
            } else {
              this.type_state = 2;
              if (response.data.estado_id > 1) {
                this.type_state = 4;
              } else {
                //let loc_id = response.data.localidad_id;
                this.postulante = response.data;
                this.postulante.informacion_academica = this.postulante.informacion_academica!=="" ? JSON.parse(this.postulante.informacion_academica):"";
                this.postulante.informacion_academica = JSON.stringify(this.postulante.informacion_academica);
                this.modalidad.id = response.data.modalidad_id;
                this.postulante.direccion_departamento_id = response.data.direccion_departamento_id;
                this.postulante.convocatoria_id =  this.convocatoria.id;
                this.getSpecialties(1);
                this.getGradoDiscapacidad(false);
                this.getEscuelas(false);
                this.getParameters();
                this.getLenguas(false);
                axios
                  .get(Service.getServe() + "provincias/activo/" + this.postulante.direccion_departamento_id,
                    Service.getHeader())
                  .then((response) => {
                    this.provincias = response.data;
                  })
                  .catch(() => {
                    console.error("Provincias no encontradas");
                  })
                //this.getMunicipios();
                axios
                  .get(Service.getServe() + "municipios/activo/" + this.postulante.provincia_id,
                    Service.getHeader())
                  .then((response) => {
                    this.municipios = response.data;
                  }
                  )
                  .catch(() => {
                    console.error("Municipios no encontrados");
                  })
             //   this.getCantones();
              //  this.canton_id = response.data.canton_id;
              //  this.getLocalidades();
              //  this.postulante.localidad_id = loc_id;
                this.cargaConvocatoria();
                this.verFotoApi(this.postulante.ruta_imagen);
              }
            }
            axios
                  .post(
                    Service.getUrlAPI() + "admision/validar/sie",
                    {
                      cedula_dni: this.postulante.cedula_dni,
                      fecha_nacimiento: this.postulante.fecha_nacimiento,
                      complemento : this.postulante.complemento,
                    },
                    Service.getHeader()
                  )
                  .then((response) => {
                    this.search_loading = false;
                    if (response.status === 200) {
                      this.postulante.informacion_academica = response.data.detalle||"";
                      this.postulante.informacion_academica = JSON.stringify(this.postulante.informacion_academica);
                    } else {
                      this.toast({ color: "info", text: "No se encontro registro de notas" });
                    }
                  })
                  .catch(() => {
                    this.search_loading = false;
                    console.error("No se pudo realizar la varificacion de notas del postulante");
                  });
          })
          .catch((err) => {
            console.log(err);
            this.load_search = false;
            console.error("Postulante no encontrado");
          });
      } else {
        this.toast({ color: "info", text: "Ingrese Cédula de identidad y Fecha de nacimiento" });
      }
    },

    confirmPostulante() {
      if (this.postulante.estado_id === 1) {
        if (this.postulante.institucion_id != "" && this.postulante.especialidad_id != "" && this.postulante.departamento_id != "" && this.postulante.lengua_id != "") {
          this.confirmDialog = true;
        } else {
          this.toast({ color: "info", text: "Complete el formulario" });
        }
      } else {
        this.toast({ color: "success", text: "Su información ya no puede ser modificada, puesto que está en la siguiente fase" });
      }
    },
    cargarDatosFotografia() {
      
      //let extension = this.fFotografia.name.slice(this.fFotografia.name.lastIndexOf('.'));
      this.postulante.tipo = "fotografia"
      this.postulante.descripcion = "fotografia postulacion";
      this.postulante.ruta_imagen = Date.now() + ".png";
      this.postulante.foto_act = this.fFotografia?"1":"0";
      this.postulante.foto = this.fFotografia;
    },
    savePostulante() {
      this.send_loading = true;
      this.postulante.convocatoria_id= this.convocatoria.id,
      //this.postulante.informacion_academica = JSON.stringify(this.postulante.informacion_academica);
      this.cargarDatosFotografia();
      let fd = new FormData();
      for (let elemento in this.postulante) {
        fd.append(elemento, this.postulante[elemento]);
      }
      axios
        .post(
          Service.getUrlAPI() + "admision/postulante/modifica",
          fd,
          {
            headers: {
              "Content-Type": "multipart/form-data",
              Authorization: Service.getTokenExt(),
            },
          }
          //this.postulante,
          //Service.getHeader()
        )
        .then((response) => {
          this.send_loading = false;
          if(response.status == 201) {
            this.type_state = 1;
            if (this.$refs.formp) this.$refs.formp.reset();
            this.send_loading = false;
            this.confirmDialog = false;
            this.toast({ color: "success", text: response.data, timeout: 3000 });
          } else {
            this.toast({ color: "warning", text: response.data });
          }

        })
        .catch((error) => {
          this.send_loading = false;
          this.toast({ color: "info", text: error.response.data });
//          console.error(err);
        });
    },

    isText: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      const codes = [45,46,192,193,196,200,201,203,204,205,207,210,211,214,217,218,220,224,225,228,232,233,235,236,237,239,242,243,246,249,250,252];
      if (codes.includes(charCode) ||
        charCode == 39 ||
        charCode == 32 ||
        charCode == 209 ||
        charCode == 241 ||
        (65 <= charCode && charCode <= 90) ||
        (97 <= charCode && charCode <= 122)
      ) {
        return true;
      } else {
        evt.preventDefault();
        return false;
      }
    },
    verFoto() {
      if (!this.fFotografia) {
        this.imageData = "No se eligio ningun archivo"
      }
      else {
        var reader = new FileReader();
        reader.readAsDataURL(this.fFotografia);
        reader.onload = () => {
          this.imageData = reader.result;
        }
      }
    },
    async verFotoApi(src) {
      if (!src) {
        this.imageData = "No se eligio ningun archivo"
